<template>
  <yd-layout>
    <yd-navbar slot="navbar" title="我的草稿">
      <a href="javascript:;" @click="goBack" slot="left">
        <yd-navbar-back-icon></yd-navbar-back-icon>
      </a>
    </yd-navbar>
      <yd-pullrefresh :callback="loadList" ref="pullrefreshList_">
      	<yd-list theme="4" v-if="list && list.length>0">
	        <rx-list-item 
	        	v-for="item in list" 
	        	:key="item.instId" 
	        	:href="{name:'startForm',params:{solId:item.solId,instId:item.instId}}" 
	        	type='link'
	    		>
						<div class="message-img">
								<span>{{getFirstLetter(item.subject)}}</span>
						</div>
	        	<div class="item-content">
		          <div slot="title" class="item-title">
		          	<span>{{item.subject}}</span>
		          	
		          </div>
				      <yd-list-other slot="other">
				        <div>
				          <span>{{item.createTime}}</span>
				          <!--<span class="list-price">{{item.createTime}}</span>-->
				        </div>
				      </yd-list-other>
	        	</div>
	        </rx-list-item>
      	</yd-list>
     </yd-pullrefresh>
     <rx-nodata :amount="list.length"></rx-nodata>
    
   
  </yd-layout>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';
import {loadRefreshList} from '@/assets/app.js';
import {ListTheme,  ListOther} from 'vue-ydui';
import listItem from '@/components/common/ListItem';
import NoData from '@/components/common/NoData';


Vue.component(listItem.name, listItem);
Vue.component(NoData.name, NoData);


export default {
  data(){
    return {
      list:[],
		  page:0,
		  total:0
    }
  },
  created() {
       this.loadList();
  },
  methods:{
    loadList(){
       var url=_baseUrl +"/mobile/bpm/getMyDrafts.do";
				var params="pageIndex="+this.page+"&pageSize=10";
				var self=this;
				loadRefreshList(url,params,this,data=>{
					//_self.tabItems[0].badge=data.total;
					//this.total=data.total;
				},true);
    },
		getFirstLetter(word){
			return word.substr(0,1).toUpperCase();
		}
  }
}
</script>

<style type="text/css" scoped>
  #scrollView{
    overflow-x: auto !important;
  }

  .icon-my-tolist:before{
    color:#5fe463 !important;
  }

  .icon-already-flow:before{
    color:#fac256 !important;;
  }
  
  .yd-flexview >>> .yd-list-title{
		font-weight: normal;
	}
	
	.yd-list-other > div>span:nth-of-type(1){
		width: 100%;
	}
  
</style>



